`, "",{"version":3,"sources":["webpack://./src/views/Popup.vue"],"names":[],"mappings":";AAwFA;IACA,YAAA;IACA,aAAA;IACA,sBAAA;IACA,6BAAA;IACA,mBAAA;AACA","sourcesContent":["\n<template>\n    <div class=\"Popup\">\n        <VButton width=\"50%\" @click.native=\"showPop('top')\">\n            Top\n        </VButton>\n        <VButton width=\"50%\" @click.native=\"showPop('right')\">\n            Right\n        </VButton>\n        <VButton width=\"50%\" @click.native=\"showPop('bottom')\">\n            Bottom\n        </VButton>\n        <VButton width=\"50%\" @click.native=\"showPop('left')\">\n            Left\n        </VButton>\n\n\n        <Popup v-model=\"show\" :position=\"side\" :width=\"width\" :height=\"height\" show-mask=\"\">\n\n        </Popup>\n    </div>\n</template>\n\n<script>\n\n    export default {\n        name: \"popup\",\n        data(){\n            return {\n                side:'top',\n                show:false,\n                sideList:['top','right','bottom','left'],\n                width:'100%',\n                height:'100%'\n            }\n        },\n\n        methods:{\n            showPop(side){\n                this.show = false\n                this.$nextTick(()=>{\n\n                    if (this.sideList.indexOf(side) < 0) side = 'top'\n\n                    switch (side){\n                        case 'top':\n                            this.width = '100%'\n                            this.height = 0\n                            break\n                        case 'right':\n                            this.width = '70%'\n                            this.height = '100%'\n                            break\n                        case 'bottom':\n                            this.width = '100%'\n                            this.height = '70%'\n                            break;\n                        case 'left':\n                            this.width = '70%'\n                            this.height = '100%'\n                            break\n                    }\n\n\n                    this.side = side\n\n\n                    setTimeout(()=>{\n                        this.show = true\n                    },350)\n                })\n\n            }\n        }\n    }\n</script>\n\n<style scoped>\n\n    .Popup {\n        height: 100%;\n        display: flex;\n        flex-direction: column;\n        justify-content: space-around;\n        align-items: center;\n    }\n</style>"],"sourceRoot":""}]);
